<template lang="html">
  <div class="count-manage-page">
    <!--  -->
    <default-head header="" path="/personcenter"></default-head>

    <div class="count-box">
      <count-item class="borderB" textLeft="" textRight="" :image="userInfo.logo"
      :isAvatar="true" :isShow="true" @click.native="changePic"></count-item>
      <count-item class="borderB" textLeft="" :textRight="bankTags[userInfo.isBank]" :warn="true"
        :isShow="true" @click.native="toBindCard"></count-item>
      <count-item class="borderB" textLeft="" @click.native="updateNickname"
      :textRight="userInfo.nickName || ''" :isShow="true"></count-item>
      <count-item class="borderB" textLeft="" :textRight="userInfo.vipNo || ''"></count-item>
      <!-- <count-item class="borderB" textLeft="" textRight="" :warn="true" :isShow="true"></count-item> -->
      <count-item class="borderB" textLeft="" textRight="" :isShow="true" @click.native="$router.push('/accountsafe')"></count-item>
      <count-item class="borderB" textLeft="" textRight="" :isShow="true" @click.native="$router.push('/complaint')"></count-item>
      <!-- <count-item class="borderB" textLeft="" textRight="" :isShow="true"></count-item>
      <count-item class="borderB" textLeft="" textRight="" :isShow="true"></count-item> -->
      <input @change="changeFile" ref="changeImg" accept="image/*" type="file" style="display:none">
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import defaultHead from '@/components/head/defaultHead'
import countItem from '@/components/personcenter/countItem'
import minutils from '@/utils/minUtils'

export default {
  data () {
    return {
      userInfo: {
        isAuth: 0,
        isBank: 0,
        isPay: 0,
        logo: '',
        nickName: '',
        vipNo: 'hzl1234567'
      },
      bankTags: ['', '', '']
    }
  },
  components: { defaultHead, countItem },
  computed: {
    userId () {
      return utils.userId()
    },
    userType () {
      return utils.getType()
    }
  },
  created () {
    this.ajaxGetAccountInfo({userId: this.userId})
  },
  methods: {
    updateNickname () {
      let self = this
      minutils.MessageBoxPrompt({
        title: '',
        options: {
          inputPlaceholder: ''
        },
        success (value, action) {
          if (!value) {
            minutils.Toast({ message: '', position: 'top' })
          } else {
            self.ajaxUpdateNickName({userId: self.userId, nickName: value})
          }
        }
      })
    },
    changePic () {
      var self = this
      minutils.MessageBoxConfirm({
        message: '',
        title: '',
        options: { confirmButtonText: '', cancelButtonText: '' },
        success (action) {
          self.$refs.changeImg.click()
        }
      })
    },
    toBindCard () {
      if (this.userInfo.isAuth !== 3) {
        let path = this.userType === 0 ? '/personrecognize' : 'companyrecognize'
        let message = this.userType === 0 ? '' : ''
        this.isRecognize(path, message)
      } else {
        this.$router.push({path: 'bindbankcard', query: {isBank: this.userInfo.isBank}})
      }
    },
    isRecognize (path, message) {
      let self = this
      minutils.MessageBoxConfirm({
        message: message + ', !',
        options: {confirmButtonText: '', cancelButtonText: ''},
        success (action) {
          self.$router.push(path)
        }
      })
    },
    changeFile (e) {
      utils.uploadPic(e.target.files, (data) => {
        this.ajaxUploadPic({fileName: data[0].fileName, fileBase64: data[0].base64Img})
      })
    },
    ajaxUpdateNickName (data) {
      api.updateNickName(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.userInfo.nickName = result.data
          minutils.MessageBox({message: ''})
        }
      })
    },
    ajaxUploadPic (data) {
      api.uploadPic(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.ajaxUpdateHeadImg({userId: this.userId, headImg: result.data})
        }
      })
    },
    ajaxUpdateHeadImg (data) {
      api.updateHeadImg(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          this.userInfo.logo = result.data
          minutils.MessageBox({message: ''})
        }
      })
    },
    ajaxGetAccountInfo (data) {
      api.getAccountInfo(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          Object.keys(this.userInfo).forEach(k => {
            if (result.data[k]) {
              this.userInfo[k] = result.data[k]
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.count-manage-page {
  padding-top: .88rem;
  .count-box {
    background: @whiteColor;
  }
}

</style>



